<template>
	<div>
		<el-row class="m-w-1280">
			<div class="m-b-20 m-t-20">
				<el-col :span="3">
                            <div class="m-b-20 btn-link-large add-btn f-l">
                              <i class="el-icon-plus"></i>&nbsp;&nbsp;添加
                            </div>
                          </el-col>
                          <el-col :span="5" :offset="11">
                              <el-input placeholder="输入快讯标题搜索" v-model="keywords">
                                <el-button slot="append" icon="search" @click="search()"></el-button>
                              </el-input>
                          </el-col>
			</div>
			<el-table
			:data="tableData"
			style="width: 100%">
			<el-table-column
		      label="编号"
		      width="180">
		      <template scope="scope">
		        <span style="margin-left: 10px">{{ scope.row.num }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="分类"
		      width="180">
		      <template scope="scope">
		        <span style="margin-left: 10px">{{ scope.row.type }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="封面"
		      width="100">
		      <template scope="scope">
		      	<img :src='scope.row.img_url'/>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="快讯标题"
		      width="180">
		      <template scope="scope">
		        <span style="margin-left: 10px">{{ scope.row.title }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="状态"
		      width="180">
		      <template scope="scope">
		        <span style="margin-left: 10px">{{ scope.row.status }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      label="创建时间"
		      width="180">
		      <template scope="scope">
		        <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column label="操作">
		      <template scope="scope">
		        <el-button
		          size="small"
		          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
		        <el-button
		          size="small"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		      </template>
		    </el-table-column>
			</el-table>
		</el-row>
              <div class="pos-rel p-t-20">
                <div class="block pages">
                  <el-pagination
                    @current-change="handleCurrentChange"
                    layout="prev, pager, next"
                    :page-size="15"
                    :current-page="currentPage"
                    :total="dataCount">
                  </el-pagination>
                </div>
              </div>
	</div>
</template>
<style>
	.el-table .cell img{
		width: 50px;
		height: 50px;
	}
</style>
<script>
  export default {
    data() {
      return {
        tableData: [{
          num: '2023434343433',
          type: '世界之窗',
          create_time: '2016-02-02 14:32:00',
          status: '被使用',
          title: '标题淘宝嗯嗯',
          img_url:'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg'
        }],
        currentPage: 1,
        dataCount: 1,
      }
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
        
        this.$confirm('确认删除吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => { 
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {

        })
      }
    },
    computed: {
      addShow() {
        return _g.getHasRule('menus-save')
      },
      editShow() {
        return _g.getHasRule('menus-update')
      },
      deleteShow() {
        return _g.getHasRule('menus-delete')
      }
    },
    handleDelete(index, row) {
        console.log(index, row);
      },
      handleCurrentChange(val) {
        this.currentPage = val
      },
  }
</script>


