<template>
  <div>
    <div class="m-b-30">
      <el-row :gutter="20">      
        <el-col :span="3">
        <router-link v-show="addShow" class="btn-link-large add-btn" to="add">
          <i class="el-icon-plus"></i>&nbsp;&nbsp;添加
        </router-link>
        </el-col>
        <el-col :span="6" :offset="11">
        <el-select v-model="value">
          <el-option
            v-for="item in options"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        </el-col>
      </el-row>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
          label="编号"
          width="180">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.num }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="类型"
          width="180">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.type }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="封面"
          width="100">
          <template scope="scope">
            <img :src='scope.row.img_url'/>
          </template>
        </el-table-column>
        <el-table-column
          label="外链"
          width="180">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.chain }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="启用"
          width="180">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.start }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="排序"
          width="180">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.sort }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
            <el-button
              size="small"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="small"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
  </div>
</template>
<style>
  .el-table .cell img{
    width: 50px;
    height: 50px;
  }
</style>
<script>
  export default {
    data() {
      return {
        tableData: [{
          num: '2023434343433',
          type: '设计中心',
          chain: 'javascript:void(0);',
          start: '√',
          sort: '100',
          img_url:'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg'
        },{
          num: '2023434343433',
          type: '设计中心',
          chain: 'javascript:void(0);',
          start: '√',
          sort: '100',
          img_url:'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg'
        }],
         options: [{
          value: '选项1',
          label: '全部类型'
        }],
        value: '选项1'
      }
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
        
        this.$confirm('确认删除吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => { 
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {

        })
      }
    },
    computed: {
      addShow() {
        return _g.getHasRule('menus-save')
      },
      editShow() {
        return _g.getHasRule('menus-update')
      },
      deleteShow() {
        return _g.getHasRule('menus-delete')
      }
    },
  }
</script>


