<template>
  <div>
    <div class="m-b-20 ovf-hd">
      <div class="fl">
        <router-link class="btn-link-large add-btn" to="userAdd">
          <i class="el-icon-plus"></i>&nbsp;&nbsp;添加用户
        </router-link>
      </div>
      <div class="fr w-200 m-l-30">
        <el-input placeholder="请输入用户名" v-model="keywords">
          <el-button slot="append" icon="search" @click="search()"></el-button>
        </el-input>
      </div>
    </div>
    <el-table
    :data="tableData"
    style="width: 100%">
      </el-table-column>
      <el-table-column
      prop="num"
      label="序号"
      width="150">
      </el-table-column>
      <el-table-column
      prop="name"
      label="用户名">
      </el-table-column>
      <el-table-column
      prop="account"
      label="账号">
      </el-table-column>
      <el-table-column
      prop="role"
      label="角色">
      </el-table-column>
      <el-table-column
      prop="phone"
      label="手机号">
      </el-table-column>
      <el-table-column
        label="操作"
        width="150">
        <template scope="scope">
          <el-button
            type="danger"
            size="small">
            删除
          </el-button>
          <router-link to="add" class="btn-link edit-btn">
            编辑
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <div class="pos-rel p-t-20">
      <div class="block pages">
        <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :page-size="15"
        :current-page="currentPage"
        :total="dataCount">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'

  export default {
    data() {
      return {
        tableData: [
          {
            id: '12',
            num: '1',
            name: '22',
            account: '22asdasd',
            phone: '18654525422',
            role: '管理员',
          }
        ],
        currentPage: 1,
        dataCount: 1,
        keywords: '',
      }
    },
    methods: {
      confirmDelete(item) {
        this.$confirm('确认删除该菜单?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.apiDelete('menus/', item.id).then((res) => {
            if (res.code == 200) {
              _g.toastMsg(this, 'success', '删除成功')
              setTimeout(() => {
                location.reload()
              }, 1500)
            } else {
              // _g.dealError(this, res)
            }
          })
        }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // })
        })
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.getAllUsers()
      },
      search() {
        this.getAllUsers()
      },
      getAllUsers() {
        this.loading = true
        this.apiGet('users?keywords=' + this.keywords + '&page=' + this.currentPage).then((res) => {
          this.loading = false
          if (res.code == 200) {
            this.tableData = res.data.list
            this.dataCount = res.data.dataCount
          } else {
            _g.dealError(this, res)
          }
        })
      }
    },
    created() {
      // this.apiGet('menus').then((res) => {
      //   if (res.code == 200) {
      //     this.tableData = res.data
      //   } else {
      //     // _g.dealError(this, res)
      //   }
      // })
    },
    computed: {
    },
    mixins: [http]
  }
</script>


