import Login from './components/account/Login.vue'
import Home from './components/Home.vue'

import publicClientList from './components/client/publicClient/list.vue'
import publicClientDetail from './components/client/publicClient/detail.vue'
import publicClientCount from './components/client/publicClient/count.vue'

import myClientList from './components/client/myClient/list.vue'
import myClientDetail from './components/client/myClient/detail.vue'
import myClientCount from './components/client/myClient/count.vue'

import infoArticle from './components/data/info/article.vue'
import infoFeedback from './components/data/info/feedback.vue'
import infoPic from './components/data/info/pic.vue'
import infoNews from './components/data/info/news.vue'
import infoWorks from './components/data/info/works.vue'
import infoVideo from './components/data/info/video.vue'
import infoAdd from './components/data/info/add.vue'

import labelLabel from './components/data/label/label.vue'
import labelLClass from './components/data/label/lClass.vue'
import serviceList from './components/data/service/list.vue'
import serviceRecord from './components/data/service/record.vue'
import serviceInfo from './components/data/service/info.vue'
import serviceOrder from './components/data/service/order.vue'
import serviceDetail from './components/data/service/detail.vue'

import demandList from './components/service/demand/demandList.vue'
import demandDetial from './components/service/demand/demandDetial.vue'
import historyList from './components/service/demand/historyList.vue'
import orderList from './components/service/order/list.vue'
import orderDetail from './components/service/order/detail.vue'
import overList from './components/service/order/overList.vue'
import flow from './components/service/finance/flow.vue'
import record from './components/service/finance/record.vue'

import roleList from './components/system/rule/roleList.vue'
import roleAdd from './components/system/rule/roleAdd.vue'
import roleEdit from './components/system/rule/roleEdit.vue'
import userList from './components/system/rule/userList.vue'
import userAdd from './components/system/rule/userAdd.vue'
import userEdit from './components/system/rule/userEdit.vue'
import config from './components/system/config/config.vue'
import email from './components/system/config/email.vue'

const routes = [
  { path: '/', component: Login, name: 'Login' },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/client/publicClient/list', component: publicClientList, name: 'publicClientList', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'publicClient' }},
      { path: '/client/publicClient/detail/:id', component: publicClientDetail, name: 'publicClientDetail', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'publicClient' }},
      { path: '/client/publicClient/count', component: publicClientCount, name: 'publicClientCount', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'publicClient' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/client/myClient/list', component: myClientList, name: 'myClientList', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'myClient' }},
      { path: '/client/myClient/detail/:id', component: myClientDetail, name: 'myClientDetail', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'myClient' }},
      { path: '/client/myClient/count', component: myClientCount, name: 'myClientCount', meta: { hideLeft: false, index_: 1, module: 'client', menu: 'myClient' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/data/info/article', component: infoArticle, name: 'infoArticle', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/feedback', component: infoFeedback, name: 'infoFeedback', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/pic', component: infoPic, name: 'infoPic', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/news', component: infoNews, name: 'infoNews', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/work', component: infoWorks, name: 'infoWorks', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/video', component: infoVideo, name: 'infoVideo', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }},
      { path: '/data/info/add', component: infoAdd, name: 'infoAdd', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'info' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/data/label/label', component: labelLabel, name: 'labelLabel', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'label' }},
      { path: '/data/label/lClass', component: labelLClass, name: 'labelLClass', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'label' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/data/service/info', component: serviceInfo, name: 'serviceInfo', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'service' }},
      { path: '/data/service/order', component: serviceOrder, name: 'serviceOrder', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'service' }},
      { path: '/data/service/detail', component: serviceDetail, name: 'serviceDetail', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'service' }},
      { path: '/data/service/list', component: serviceList, name: 'serviceList', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'service' }},
      { path: '/data/service/record', component: serviceRecord, name: 'serviceRecord', meta: { hideLeft: false, index_: 1, module: 'data', menu: 'service' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/service/demand/demandlist', component: demandList, name: 'demandList', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'demand' }},
      { path: '/service/demand/demanddetial', component: demandDetial, name: 'demandDetial', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'demand' }},
      { path: '/service/demand/historylist', component: historyList, name: 'historyList', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'demand' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/service/order/list', component: orderList, name: 'orderList', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'order' }},
      { path: '/service/order/detail', component: orderDetail, name: 'orderDetail', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'order' }},
      { path: '/service/order/overList', component: overList, name: 'overList', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'order' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/service/finance/flow', component: flow, name: 'flow', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'finance' }},
      { path: '/service/finance/record', component: record, name: 'record', meta: { hideLeft: false, index_: 1, module: 'service', menu: 'finance' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/system/rule/roleList', component: roleList, name: 'roleList', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'role' }},
      { path: '/system/rule/roleAdd', component: roleAdd, name: 'roleAdd', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'role' }},
      { path: '/system/rule/roleEdit', component: roleEdit, name: 'roleEdit', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'role' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/system/rule/userList', component: userList, name: 'userList', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'rule' }},
      { path: '/system/rule/userAdd', component: userAdd, name: 'userAdd', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'rule' }},
      { path: '/system/rule/userEdit', component: userEdit, name: 'userEdit', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'rule' }}
    ]
  },
  {
    path: '/home',
    component: Home,
    children: [
      { path: '/system/config/config', component: config, name: 'config', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'config' }},
      { path: '/system/config/email', component: email, name: 'email', meta: { hideLeft: false, index_: 1, module: 'system', menu: 'config' }}
    ]
  }
]
export default routes



