<template>
  <div class="all">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="作品名称">
        <el-input v-model="form.works_name"></el-input>
      </el-form-item>
      <el-form-item label="所属公司">
        <el-select v-model="form.company" placeholder="请选择服务方">
          <el-option label="公司一" value="one"></el-option>
          <el-option label="公司二" value="two"></el-option>
        </el-select>
      </el-form-item>
       <el-form-item label="Seo Title">
        <el-input v-model="form.seo_title"></el-input>
      </el-form-item>
       <el-form-item label="Seo Keyword">
        <el-input v-model="form.keyword"></el-input>
      </el-form-item>
      <el-form-item label="Seo Description">
        <el-input v-model="form.seo_des"></el-input>
          </el-form-item>
        </el-form-item>
      <el-form-item label="缩略图">
          <img src="form.samall_pic"><el-button type="primary" @click="new_update">重新上传</el-button>
      </el-form-item>
      </el-form-item>
       <el-form-item label="轮播图">
          <img src="form.pic1"><img src="form.pic2"><img src="form.pic3"><img src="form.pic4"><el-button type="primary" @click="continue_update">继续上传</el-button>
        </el-form-item>
      <el-form-item label="详情描述">
        <el-input type="textarea" v-model="form.detial_des"></el-input>
      </el-form-item>
      <el-form-item label="状态">
        <el-radio-group v-model="form.if_carriage">
          <el-radio label="已上架"></el-radio>
          <el-radio label="已下架"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="状态">
        <el-radio-group v-model="form.if_top">
          <el-radio label="不置顶"></el-radio>
          <el-radio label="置顶"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">发布</el-button>
        <el-button>返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<style>
  
</style>
<script>
  export default {
    data() {
      return {
        form: {
          works_name: '',
          company: '',
          seo_title: '',
          keyword: '',
          seo_des: '',
          if_carriage: '',
          if_top: '',
          detial_des: '',
          samall_pic: 'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg',
          pic1: 'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg',
          pic2: 'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg',
          pic3: 'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg',
          pic4: 'http://www.people.com.cn/mediafile/pic/20170104/8/14773429711993116816.jpg'
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      new_update() {

      },
      continue_update() {
        
      }
    }
  }
</script>


