<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="2"><div class="tx-c">ID</div></el-col>
      <el-col :span="5"><div class="tx-c">服务名称</div></el-col>
      <el-col :span="5"><div class="tx-c">Email</div></el-col>
      <el-col :span="4"><div class="tx-c">Host</div></el-col>
      <el-col :span="2"><div class="tx-c">端口</div></el-col>
      <el-col :span="4"><div class="tx-c">备注</div></el-col>
      <el-col :span="2"><div class="tx-c">操作</div></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="2"><div class="tx-c h-40">{{emailMsg.id}}</div></el-col>
      <el-col :span="5"><div class="tx-c h-40">{{emailMsg.name}}</div></el-col>
      <el-col :span="5"><div class="tx-c h-40">{{emailMsg.email}}</div></el-col>
      <el-col :span="4"><div class="tx-c h-40">{{emailMsg.host}}</div></el-col>
      <el-col :span="2"><div class="tx-c h-40">{{emailMsg.port}}</div></el-col>
      <el-col :span="4"><div class="tx-c h-40">{{emailMsg.remark}}</div></el-col>
      <el-col :span="2">
        <div class="tx-c h-40">
          <el-button
            type="primary"
            size="small"
            @click="editInfo()">
            编辑
          </el-button>
        </div>
      </el-col>
    </el-row>
    <editInfo ref="editInfo" :info="emailMsg"></editInfo>
  </div>
</template>
<style type="text/css">
  .form-checkbox:first-child{
    margin-left: 15px;
  }
</style>
<script>
  import http from '../../../assets/js/http'
  import editInfo from './edit.vue'
  export default {
    data() {
      return {
        emailMsg: {}
      }
    },
    methods: {
      editInfo() {
        this.$refs.editInfo.open()
      }
    },
    created() {
      //获取数据
      this.emailMsg = {
        email: 'test.com',
        name: '服务名称',
        id: '112',
        host: 'baidu.com',
        port: '5',
        remark: '备注信息'
      }
    },
    components: {
      editInfo
    },
    mixins: [http]
  }
</script>


