<template>
  <div class="m-l-50 m-t-30 w-500">
    <el-form ref="form" :model="form" :rules="rules" label-width="130px">
      <el-form-item label="账号" prop="u_account">
        <el-input v-model.trim="form.u_account" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="u_username">
        <el-input v-model.trim="form.u_username" class="h-40 w-200" :maxlength="12"></el-input>
      </el-form-item>
      <el-form-item label="用户角色" prop="r_id">
        <el-select v-model="form.r_id" placeholder="请选择角色" class="w-200">
          <el-option v-for="item in roleList" :label="item.title" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="手机号码">
        <el-input v-model.trim="form.u_phone" class="h-40 w-200" :maxlength="11"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="u_pwd">
        <el-input v-model.trim="form.u_pwd" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="跟进人员上限">
        <el-input v-model.trim="form.u_flow" class="h-40 w-200"></el-input>&nbsp;&nbsp;人
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add()" :loading="isLoading">提交</el-button>
        <el-button @click="goback()">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<style type="text/css">
  .form-checkbox:first-child{
    margin-left: 15px;
  }
</style>
<script>
  import http from '../../../assets/js/http'
  export default {
    data() {
      return {
        form: {
          u_account: '',
          u_username: '',
          u_pwd: '',
          u_phone: '',
          s_id: null,
          u_flow: '',
          groups: [],
        },
        roleList: [],
        rules: {
          u_account: [
            { required: true, message: '请输入账号' }
          ],
          u_username: [
            { required: true, message: '请输入用户名' }
          ],
          u_pwd: [
            { required: true, message: '请输入用户密码' }
          ],
          r_id: [
            { required: true, message: '请选择用户角色' }
          ]
        }
      }
    },
    methods: {
      add() {
        this.$refs.form.validate((pass) => {
          if (pass) {
            this.isLoading = !this.isLoading
            this.apiPost('users', this.form).then((res) => {
              if (res.code == 200) {
                _g.toastMsg(this, 'success', '添加成功')
                _g.clearVuex('setUsers')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              } else {
                this.isLoading = !this.isLoading
                _g.dealError(this, res)
              }
            })
          }
        })
      },
      goback() {
        router.go(-1)
      },
      // 获取角色列表
      getAllRoles() {
        
      },
      // 获取数据
      getData() {
        
      },
    },
    created() {
      // this.getAllRoles()
      // this.getData()
    },
    components: {

    },
    mixins: [http]
  }
</script>


