<template>
	<div>
    <el-row>
    <div class="m-b-20 ovf-hd">
      <div class="fr w-800 m-l-30">
      	<el-row type='flex' justify='end' :gutter='10'>
			<el-col :span='20'>
				<el-select v-model="coop_value" placeholder="请选择合作模式">
				    <el-option
				      v-for="item in coop_options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</el-col>
			<el-col :span='20'>
				<el-select v-model="contract_value" placeholder="请选择合同状态">
				    <el-option
				      v-for="item in contract_options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</el-col>
			<el-col :span='20'>
				<el-select v-model="order_value" placeholder="请选择订单状态">
				    <el-option
				      v-for="item in order_options"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</el-col>
			<el-col>
				<el-input placeholder="请选择客户、手机号" v-model="keywords">
			        <el-button slot="append" icon="search" @click="search()"></el-button>
			    </el-input>
			</el-col>
      	</el-row>
      	
        
      </div>
    </div>
    </el-row>
		<el-table
	      :data="tableData"
	      style="width: 100%">
	      <el-table-column
	        prop="num"
	        label="订单编号"
	        width="100">
	      </el-table-column>
	      <el-table-column
	        prop="title"
	        label="订单标题"
	        width="180">
	      </el-table-column>
	      <el-table-column
	        prop="pattern"
	        label="合作模式"
	        width="180">
	      </el-table-column>
	      <el-table-column
	        prop="sum"
	        label="金额"
	        width="80">
	      </el-table-column>
	      <el-table-column
	        prop="client"
	        label="需求方"
	        width="150">
	      </el-table-column>
	      <el-table-column
	        prop="servicer"
	        label="服务方">
	      </el-table-column>
        <el-table-column
          prop="create_time"
          label="建立日期">
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态">
        </el-table-column>
	  </el-table>
     <div class="pos-rel p-t-20">
      <div class="block pages">
        <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :page-size="15"
        :current-page="currentPage"
        :total="dataCount">
        </el-pagination>
      </div>
    </div>
	</div>
</template>
<style type="text/css">
  .page-part{
    margin-top: 10px;
  }
</style>

<script>
	export default{
	    data() {
	      return {
          currentPage: 1,
          dataCount: 1,
	        keywords: '',
	        tableData: [
	        {
	            client: '搜索公司公司',
	            create_time: '2016-01-01',
              	status:'已认证',
              	pattern: '跟进记录跟进记录跟进记录',
	            servicer: '上海ren',
	            sum:'34344',
              	title: '12345678901',
              	num: 'sjisisis'
	        },
            {
                client: '搜索公司公司',
	            create_time: '2016-01-01',
              	mark_status: '未匹配',
              	status:'已认证',
              	pattern: '跟进记录跟进记录跟进记录',
	            servicer: '上海ren',
	            sum:'34344',
              	title: '12345678901',
              	num: 'sjisisis'
            }],
            contract_options: [{
	          value: '选项1',
	          label: '黄金糕'
	        }, {
	          value: '选项2',
	          label: '双皮奶'
	        }, {
	          value: '选项3',
	          label: '蚵仔煎'
	        }, {
	          value: '选项4',
	          label: '龙须面'
	        }, {
	          value: '选项5',
	          label: '北京烤鸭'
	        }],
	        order_options: [{
	          value: '选项1',
	          label: '黄金糕'
	        }, {
	          value: '选项2',
	          label: '双皮奶'
	        }, {
	          value: '选项3',
	          label: '蚵仔煎'
	        }, {
	          value: '选项4',
	          label: '龙须面'
	        }, {
	          value: '选项5',
	          label: '北京烤鸭'
	        }],
			coop_options: [{
	          value: '选项1',
	          label: '黄金糕'
	        }, {
	          value: '选项2',
	          label: '双皮奶'
	        }, {
	          value: '选项3',
	          label: '蚵仔煎'
	        }, {
	          value: '选项4',
	          label: '龙须面'
	        }, {
	          value: '选项5',
	          label: '北京烤鸭'
	        }],
	        coop_value: '',
        	contract_value: '',
        	order_value: ''
	      }
	    },
	    methods: {
        search() {
          this.getAllUsers()
        },
        getAllUsers() {
          this.loading = true
          this.apiGet('users?keywords=' + this.keywords + '&page=' + this.currentPage).then((res) => {
            this.loading = false
            if (res.code == 200) {
              this.tableData = res.data.list
              this.dataCount = res.data.dataCount
            } else {
              _g.dealError(this, res)
            }
          })
        },
	    handleIconClick(ev) {
	      console.log(ev);
	    },
        handleSizeChange(val) {
          console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
          this.currentPage = val;
          console.log(`当前页: ${val}`);
        }
		}
	}
</script>


