<template>
  <div>
    <div class="m-b-20 ovf-hd">
      <div class="fr w-300">
        <el-input placeholder="请输入订单、交易方名称搜索" v-model="keywords">
          <el-button slot="append" icon="search" @click="search()"></el-button>
        </el-input>
      </div>
    </div>
    <el-table
    :data="tableData"
    style="width: 100%">
      <el-table-column
      prop="flow_num"
      label="编号">
      </el-table-column>
      <el-table-column
      prop="flow_order"
      label="订单名称">
      </el-table-column>
      <el-table-column
      prop="money_name"
      label="款项名">
      </el-table-column>
      <el-table-column
      prop="money"
      label="金额">
      </el-table-column>
      <el-table-column
      prop="client"
      label="交易方">
      </el-table-column>
      <el-table-column
