            flow_order: '22',
            money_name: '收款',
            time: '10-25 24:00',
            remark: '尾款',
            summary: '收款摘要',
            client: '交易方',
            money: '465'
          }
        ],
        currentPage: 1,
        dataCount: 1,
      }
    },
    methods: {
      handleCurrentChange(val) {
        this.currentPage = val
      },
      getAllData() {
        
      }
    },
    created() {
      this.getAllData
    },
    computed: {
    },
    mixins: [http]
  }
</script>


