        	addTime: '2016-12-12' + ' ' + '16:43'
        }],
        dialogVisible: false
      }
    },
    methods: {
      addRecord() {
      	var self = this;
      	var dateTime = new Date();
      	if(self.text) {
      		var temp = {
      			sturdyRecord: self.text,
        		addName: '小明',
        		addTime: dateTime.getFullYear() + '-' + (dateTime.getMonth() + 1) + '-' + dateTime.getDate() + ' ' + dateTime.getHours() + ':' + dateTime.getMinutes()
      		}
      		self.sturdyData.push(temp);
      	}
      },
      selectStaff() {
      	this.$refs.staff.open()
      }
    },
    created() {
    },
    components: {
     staff
    },
    mixins: [http]
  }
