<template>
  <div>
    <div class="m-b-10">
      <el-row :gutter="20">
          <div class="m-t-10 m-b-15" style="background:red">
            <el-col :span="2">
              <a href="#/data/info/article"><span class="older">文件管理</span></a>
            </el-col>
            <el-col :span="2">          
                <a class="now" href="#/data/info/feedback"><u>用户反馈</u>
                </a>         
            </el-col>
            <el-col :span="5" :offset="13">
            <el-input placeholder="输入联系人、快讯标题搜索" v-model="keywords">
              <el-button slot="append" icon="search" @click="search()"></el-button>
            </el-input>
            </el-col>
          </div>
      </el-row>
    </div>
    <el-table
  	class='m-w-1280'
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        label="编号"
        width="100">
        <template scope="scope">
          <span class="span-back">{{ scope.row.num }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="联系人"
        width="100">
        <template scope="scope">
        	<span class="span-back">{{ scope.row.linker }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="联系电话"
        width="160">
        <template scope="scope">
          <span class="span-back">{{ scope.row.phone }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="联系邮件"
        width="160">
        <template scope="scope">
          <span class="span-back">{{ scope.row.email }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="反馈标题"
        width="180">
        <template scope="scope">
          <span class="span-back now ifclick">{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="内容"
        width="180">
        <template scope="scope">
          <span class="span-back" @click="watchTitle(scope.$index, scope.row)">{{ scope.row.content }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="备注"
        width="180">
        <template scope="scope">
          <span class="span-back">{{ scope.row.remark }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="状态"
        width="100">
        <template scope="scope">
          <span class="span-back">{{ scope.row.statement }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="生成时间"
        width="180">
        <template scope="scope">
          <span class="span-back">{{ scope.row.create_time }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    	<div class="pos-rel p-t-20">
        <div class="block pages">
          <el-pagination
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :page-size="15"
          :current-page="currentPage"
          :total="dataCount">
          </el-pagination>
        </div>
      </div>
  </div>
</template>
<style>
  .span-back{
    margin-left: 0
  }
  .ifclick{
    cursor:pointer
  }
</style>
<script>

  export default{
    data() {
      return {
        options4: [],
        value9: [],
        list: [],
        loading: false,
        tableData: [{
          num: '202',
          title: 'hello12333333333',
          content: '王小虎',
          create_time: '2015-03-03 12:22:22',
          email: '2034343@qq.com',
          linker: '王小虎',
          statement: '状态123',
          phone:'15779501136',
          remark: 'testtest'
        }],
        currentPage: 1,
        dataCount: 1,
      }
    },
    methods: {
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleCurrentChange(val) {
        this.currentPage = val
      },
      watchTitle(index,row){
        console.log(index, row);
      }
    }
  }
</script>


