<template>
  <div class="m-l-50 m-t-30 w-500">
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="角色" prop="role_name">
        <el-input v-model.trim="form.role_name" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="描述">
        <el-input type="textarea" v-model.trim="form.remark" :rows="4" class="w-300"></el-input>
      </el-form-item>
      <el-form-item label="权限节点" class="roleCheckbox">
        <el-checkbox-group v-model="selectedOptions">
          <el-checkbox v-for="item in ruleOptions" :label="item.title" class="fl w-120"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="edit()" :loading="isLoading">提交</el-button>
        <el-button @click="goback()">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import http from '../../../assets/js/http'
  export default {
    data() {
      return {
        form: {
          role_name: '',
          remark: '',
          power: '',
        },
        selectedOptions: [],
        ruleOptions: [
          {
            title: '权限1',
            id: '1'
          },
          {
            title: '权限2',
            id: '2'
          },
          {
            title: '权限3',
            id: '3'
          },
          {
            title: '权限4',
            id: '4'
          },
        ],
        rules: {
          role_name: [
            { required: true, message: '请输入角色名' }
          ],
        }
      }
    },
    methods: {
      selectCheckbox() {
        let temp = false
        _(this.selectedOptions).forEach((res1) => {
          _(this.ruleOptions).forEach((res2) => {
            if (res1 == res2.title) {
              this.selectedId.push(res2.id)
            }
          })
        })
        if (this.selectedId.length) {
          let string = this.selectedId.toString()
          this.form.power = string
          temp = true
        }
        this.selectedId = []
        return temp
      },
      edit() {
        if (!this.selectCheckbox()) {
          _g.toastMsg(this, 'warning', '请选择权限节点')
          return
        }
