        //           this.goback()
        //         }, 1500)
        //       } else {
        //         this.isLoading = !this.isLoading
        //         _g.dealError(this, res)
        //       }
        //     })
        //   }
        // })
      },
      goback() {
        router.go(-1)
      },
      // 获取权限列表
      getAllPower() {
        
