<template>
  <div class="m-l-50 m-t-30 w-500">
    <el-form ref="form" :model="form" :rules="rules" label-width="130px">
      <el-form-item label="系统名称" prop="system_name">
        <el-input v-model.trim="form.system_name" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="版权信息">
        <el-input v-model.trim="form.system_info" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="LOGO">
        <el-upload
        :action="uploadUrl"
        type="drag"
        :thumbnail-mode="true"
        :on-preview="viewPic"
        :on-remove="handleRemove"
        :on-success="uploadSuccess"
        :on-error="uploadFail"
        :default-file-list="fileList">
          <i class="el-icon-upload"></i>
          <div class="el-dragger__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add()">提交</el-button>
        <el-button @click="goback()">返回</el-button>
      </el-form-item>
    </el-form>
    <preview ref="preview" :url="propsImg"></preview>
  </div>
</template>
<style type="text/css">
  .form-checkbox:first-child{
    margin-left: 15px;
  }
</style>
<script>
  import http from '../../../assets/js/http'
  import preview from './preview.vue'
  export default {
    data() {
      return {
        form: {
          system_name: '',
          system_info: '',
          SYSTEM_LOGO: ''
        },
        uploadUrl: '',
        fileList: [],
        propsImg: '',
        rules: {
          system_name: [
            { required: true, message: '请输入系统名称' }
          ]
        }
      }
    },
    methods: {
      add() {
        this.$refs.form.validate((pass) => {
          if (pass) {
            this.isLoading = !this.isLoading
            this.apiPost('users', this.form).then((res) => {
              if (res.code == 200) {
                _g.toastMsg(this, 'success', '添加成功')
                _g.clearVuex('setUsers')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              } else {
                this.isLoading = !this.isLoading
                _g.dealError(this, res)
              }
            })
          }
        })
      },
      goback() {
        router.go(-1)
      },
      uploadSuccess(res, file, fileList) {
        // console.log('res = ',_g.j2s(res))
        this.form.SYSTEM_LOGO = res.data
        let data = {
          name: '图片',
          url: window.imgUrl + res.data
        }
        if (this.fileList.length) {
          this.fileList[0] = data
        } else {
          this.fileList.push(data)
        }
      },
      uploadFail(err, res, file) {
        console.log('err = ', _g.j2s(err))
        console.log('res = ', _g.j2s(res))
      },
      handleRemove(file, fileList) {
        console.log('file = ', file)
        console.log('fileList = ', fileList)
      },
      viewPic() {
        this.propsImg = this.fileList[0].url
        //有问题
        this.$refs.preview.open()
      },
    },
    created() {
      this.uploadUrl = window.HOST + '/Upload'
      // this.apiPost('base/getConfigs').then((res) => {
      //   if (res.code == 200) {
      //     this.form.system_name = res.data.system_name
      //     this.form.system_info = res.data.system_info
      //     if (res.data.SYSTEM_LOGO) {
      //       let img = window.imgUrl + res.data.SYSTEM_LOGO
      //       this.fileList.push({ name: '图片', url: img })
      //       this.form.SYSTEM_LOGO = res.data.SYSTEM_LOGO
      //     }
      //   } else {
      //     _g.dealError(this, res)
      //   }
      // })
    },
    components: {
      preview
    },
    mixins: [http]
  }
</script>


